import { Cart } from '@/checkout/data/models/cart';
import { getCheckoutData } from '@/checkout/lib/events';

type CartData = Cart | null;

export function isVirtualCart(data?: CartData) {
  if (data === undefined) {
    const latestData = getCheckoutData();
    return Boolean(latestData?.isVirtual);
  }
  return Boolean(data?.isVirtual);
}

export function isEmptyCart(data: CartData) {
  return !data || data.isEmpty;
}

export function getCartShippingMethod(data: CartData) {
  if (!data) return null;
  const shippingAddresses = data.shippingAddresses || [];
  if (shippingAddresses.length === 0) return null;
  return shippingAddresses[0]?.selectedShippingMethod;
}

export function getCartAddress(
  data: CartData,
  type: 'shipping' | 'billing' = 'shipping'
): Record<string, any> | null {
  if (!data) return null;

  const address =
    type === 'shipping' ? data.shippingAddresses?.[0] : data.billingAddress;

  return address ?? null;
}

export function getCartPaymentMethod(data: CartData) {
  if (!data) return null;
  const { selectedPaymentMethod } = data;
  if (!selectedPaymentMethod || !selectedPaymentMethod?.code) return null;

  return data.selectedPaymentMethod;
}
